.hero {
    height: calc(100vh - 80px);
    overflow: hidden;
    position: relative;
    background: linear-gradient(180deg, #0c0c1d, #111132);
    
    .wrapper {
        max-width: var(--max-width-desktop);
        margin: auto;
        height: 100%;

        .textContainer {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 40px;
        }
    }

    .imageContainer {
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
    } 
}