.sidebar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: black;
    background-color: white ;
    
    
    .bg {
        position: fixed;
        left: 0px;
        top: 0px;
        width: 350px;
        height: 100vh;
        z-index: 2;
        background-color: white;
        
        .links{
            position: absolute;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 10px;  
        }
        
        a {
            width: 60%;
            text-align: center;
            background-color: white;
            font-size: 20px;
            padding: 5px 0;
            border-radius: 50px;
        }
    }
    
    button {
        position: fixed;
        top: 18px;
        left: 20px;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        background-color: transparent;
        background-color: red;
        border: none;
        color: white;
        font-size: 28px;
        z-index: 3;
        cursor: pointer;
    }
}